:root {
	--timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	--animation-duration: 250ms;
 }
 
 html {
	box-sizing: border-box;
 }
 
 *,
 *::before,
 *::after {
	box-sizing: inherit;
 }
 body {
	min-height: 100vh;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
	  Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
	background-color: #fff;
	color: #212121;
 }

 ul{
	margin: 0;
	padding: 0;
	list-style: none;
 }
 
 .feedbeck-form{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
 }
 .form__input{
	max-width: 300px;
	color: dimgray;
 }
 .form__label{
	 margin-top: 20px;
	 display: flex;
	 flex-direction: column;
 }
 .form__textarea{
	 margin-bottom: 20px;
	 max-width: 300px;
	 color: dimgray;
 }
 .form__btn{
	 max-width: 200px;
	 padding-bottom: 10px;
	 padding-top: 10px;
	 background-color: rgba(0, 0, 255, 0.39);
	 color: white;
	 border-radius: 4px;
	 border-color: transparent;
 }
 

  .gallery {
	display: grid;
	width: 1200px;
	grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
	grid-auto-rows: 240px;
	grid-gap: 12px;
	padding: 0;
	list-style: none;
	margin-left: auto;
	margin-right: auto;
 } 
 
 .gallery__item {
	position: relative;
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
	  0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
 }
 
 .gallery__image:hover {
	transform: scale(1.03);
 }
 
 .gallery__image {
	height: 100%;
	width: 100%;
	object-fit: cover;
	transition: transform var(--animation-duration) var(--timing-function);
 }
 
 .gallery__link {
	display: block;
	text-decoration: none;
	height: 100%;
 }
 
 .gallery__link:hover {
	cursor: zoom-in;
 }
 
 /* Lightbox */
 .lightbox {
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	opacity: 0;
	pointer-events: none;
	transition: opacity var(--animation-duration) var(--timing-function);
 }
 
 .lightbox.is-open {
	opacity: 1;
	pointer-events: initial;
 }
 
  .lightbox__overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.8);
 }
 
 .lightbox__content {
	transform: scale(0.95);
	transition: transform var(--animation-duration) var(--timing-function);
 }
 
 .lightbox.is-open .lightbox__content {
	transform: scale(1);
 }
 
 .lightbox__image {
	display: block;
	width: auto;
	height: auto;
	max-height: 100vh;
	max-width: 100vw;
 }
 
  .lightbox__button {
	position: absolute;
	top: 8px;
	right: 8px;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 48px;
	height: 48px;
	padding: 0;
	margin: 0;
	border: none;
	border-radius: 50%;
	background-color: transparent;
	color: #fff;
	cursor: pointer;
	transition: background-color var(--animation-duration) var(--timing-function);
	/* outline: none; */
	/* background: url('../img/icon-close.svg');
	background-position: center; */
	/* background-size: 60%;
	background-repeat: no-repeat; */
 } 
  
 .lightbox__button:hover,
 .lightbox__button:focus {
	background-color: rgba(0, 0, 0, 0.5);
 } 
